@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Poppins' ,sans-serif;
  font-size: 1rem;
}

.nav{
  color:white;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap:.5em;
  background-color: #F55A5A;
}
.travel-card{
  width: 48%;
  display: flex;
  background-color:#e2e0e0 ;
  align-items: center;
  border-radius: 10px;
  justify-content: center;
  
  
}
img{
  width:200px;
 border-radius: 10px;
 margin:.5em;
  
  
}
.travel-container{

  margin: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap:1rem;
  flex-wrap: wrap;
  
}
.description{
  align-self: stretch;
  margin-top:1em
}
h4,p{
  margin-top:1em;
}
.country{
  margin:0 ;
  letter-spacing: 3px;
}
.location{
  display: flex;
  gap:.3em;
  align-items: center;
  margin-bottom: .5em;

}
i{
  color: #F55A5A;
}
@media screen  and (max-width:1000px){
  h4,p{
    font-size: 0.9rem;
  }
  h2{
    font-size: 0.95rem;
  }
  .description{
    margin:.5em
  }
  .travel-container{
    flex-direction: column;
  }
  .travel-card{
    width:100%;
  }
  .description{
    align-self: stretch;
    margin-top:2em
  }
  img{
    width:150px;
   
   margin:.5em;
  }
    
  
 
}